import neilModal from '../../components/neil-modal/neil-modal.vue';
import { request } from '../../utils/request.js';
export default {
	components: {
		neilModal
	},
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: false,
			//  用户协议框的显示隐藏
			show2: false,
			// 获取到的用户信息
			userInfo: {},
			show1: true,
			// 用户扫码传递的参数
			pid: ''
		};
	},
	onLoad(e) {
		this.pid = Number(uni.getStorageSync('pid'));
		console.log(typeof this.pid);
		console.log(this.pid);
	},
	methods: {
		modalTap: function(e) {
			//   展示用户协议框
			this.show2 = true;
		},
		cancelAuth() {
			this.show2 = false;
		},
		// 获取用户信息 API 在小程序可直接使用，在 5+App 里面需要先登录才能调用
		getUserInfo() {
			uni.getUserInfo({
				provider: 'weixin',
				success: result => {
					console.log('getUserInfo success', result);
					this.userInfo = result.userInfo;
				},
				fail: error => {
					console.log('getUserInfo fail', error);
					let content = error.errMsg;
					if (~content.indexOf('uni.login')) {
						content = '请在登录页面完成登录操作';
					}

					uni.getSetting({
						success: res => {
							let authStatus = res.authSetting['scope.userInfo'];
							if (!authStatus) {
								uni.showModal({
									title: '授权失败',
									content: 'Hello uni-app需要获取您的用户信息，请在设置界面打开相关权限',
									success: res => {
										if (res.confirm) {
											uni.openSetting();
										}
									}
								});
							} else {
								uni.showModal({
									title: '获取用户信息失败',
									content: '错误原因' + content,
									showCancel: false
								});
							}
						}
					});








				}
			});
		},
		mpGetUserInfo(result) {
			if (result.detail.errMsg !== 'getUserInfo:ok') {
				// uni.showModal({
				// 	title: '获取用户信息失败',
				// 	content: '错误原因' + result.detail.errMsg,
				// 	showCancel: false
				// });
				//用户点击拒绝授权，跳转到设置页，引导用户授权
				this.show1 = false;
				wx.openSetting({
					success() {
						wx.authorize({
							scope: 'scope.userinfo',
							success() {
								that.getUserInfo();
							}
						});
					}
				});
				return;
			}
			this.userInfo = result.detail.userInfo;
			const that = this;

			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					uni.setStorageSync('code', loginRes.code);
					uni.setStorageSync('pid', Number(uni.getStorageSync('pid')));
					that.userInfo.code = loginRes.code;
					uni.setStorageSync('userinfo', result.detail.userInfo);
					// 优化过得代码
					request({
						url: '/api/user/MiniProgramLogin',
						data: {
							code: that.userInfo.code,
							nickname: that.userInfo.nickName,
							headimage: that.userInfo.avatarUrl,
							gender: that.userInfo.gender,
							province: that.userInfo.province,
							city: that.userInfo.city,
							country: that.userInfo.country,
							// 注意this指向
							pid: that.pid
						},
						method: 'POST'
					}).then(res => {
						if (res.data.code != 1) {
							uni.showModal({
								title: '登录失败',
								showCancel: false
							});
						} else {
							// uni.setStorageSync('token', res.data.userinfo.token)
							uni.setStorageSync('token', res.data.data.userinfo.token);
							uni.setStorageSync('login', res.data.data.userinfo);
							// 登陆成功 跳转到tab首页
							uni.switchTab({
								url: '/pages/anchor/anchor'
							});
						}
					});
				}
			});
		}
	}
};
